body{
    font-family: Avenir;
}

h1, h3, p, button {
    color: #6f6a91;
}

h1 {
    display:-webkit-flex;
    display:flex; 
    -webkit-align-items: center; 
            align-items: center;
}

.logo {
    height:70px;
    padding:10px;
}


.todoListMain .header input {
    padding:10px;
    font-size:16px;
    border: 2px solid #FFF;
    width: 165px;
}

.todoListMain .header button {
    padding: 10px;
    font-size: 16px;
    margin: 10px;
    margin-right: 0px;
    border-radius:4px; 
    background-color: peachpuff;
    border-color: peachpuff;
    
}

.todoListMain .header button:hover {
    background-color:#ea8b8b;
    border-color: #ea8b8b;
    color:white;
    border-width:2px;
    border-radius:4px; 
    cursor:pointer;
  
}

.todoListMain .theList li {
    color: #333;
    background-color: rgba(255, 255, 255, .5);
    padding: 15px;
    margin-bottom: 15px;
    border-radius: 5px;
    list-style:none;

    transition: background-color .2s ease-out;
}

.todoListMain .theList li:hover {
    background-color:#6f6a91;
    cursor: pointer;
    color:white;

}

ul.theList{
    padding: 0;
}

body{
    background-color:beige;
}
